// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import VueRouter from 'vue-router' //
import routes from './route/' //

import store from './store/' //

import VueResource from 'vue-resource' //ajax
import ElementUI from 'element-ui' //
import 'element-theme-default'; //

Vue.use(VueRouter)
Vue.use(VueResource)
Vue.use(ElementUI)
    //Vue.http.options.emulateJSON = true;//contenttypeapplication/x-www-form-urlencodedapplication/json
const router = new VueRouter({
    routes //  routes: routes
})
router.beforeEach(({ meta, path }, from, next) => {
        var { auth = true } = meta
        var isLogin = Boolean(store.state.sessionid) //true false
        // console.log(auth);
        // console.log(store.state.sessionid);
        // console.log(isLogin);
        if(path=='/login'){
            store.commit('login_ify');
        }else{
            store.commit('login_ifn');
        }
        if (auth && !isLogin && path !== '/login') {            
            return next({ path: '/login' })
        } 
        next()
    })
    /* eslint-disable no-new */
new Vue({
    el: '#app',
    template: '<App/>',
    components: { App },
    router,
    store
})



