<template>
    <div>
        <!-- ajax -->
        <div class="right_head">
            
        </div>
        <div class="right_body">
            <template>
                <el-table :data="products" ref="products" border stripe style="width: 100%" @selection-change="handleSelectionChange">
                    <el-table-column type="selection" width="55" :selectable="if_check">
                    </el-table-column>
                    <el-table-column prop="product_id" label="" resizable min-width="120px">
                    </el-table-column>
                    <el-table-column prop="name" label="" resizable min-width="100px">
                    </el-table-column>
                    <el-table-column prop="intro" label="" resizable min-width="170px">
                    </el-table-column>
                </el-table>
            </template>
            <el-form label-width="125px" class="demo-ruleForm">
                <div class="mar_t20 t_c">
                    <el-col :xs="24" :sm="24" :md="24" :lg="24">
                        <el-button type="primary" @click="mend"></el-button>
                        <el-button @click="goback"></el-button>
                    </el-col>
                </div>
            </el-form>
        </div>
        <load :visible="loading"></load>
        <toast :msg="toastmsg" :visible="visible_toast" @on-visible-change="onVisibleChange" @on-msg-change="onMsgChange"></toast>
    </div>
</template>
<script>
import load from '../../components/load'
import toast from '../../components/toast'
export default {
    name: 'channel_pro',
    components: {
        load,
        toast
    },
    data() {
        return {
            loading: false, //load
            toastmsg: '', //
            visible_toast: false, //toast
            check_all: false, //
            products: [], //ajax
            product: [], // (product_id)
            product_start:0,//
            products_url: location.protocol + '//' + location.host + '/qudao/v1/api/qd/' + this.$route.params.userid + '/product_info', //  
        }
    },
    created: function() {
        this.get_products(); //
    },
    methods: {
        //toast
        onVisibleChange(val) {
            this.visible_toast = val; //
        },
        //toast
        onMsgChange(val) {
            this.toastmsg = val; //
        },
        mend: function() {
            var _this = this;
            if (_this.product.length <= _this.product_start) {
                _this.toastmsg = '';
                _this.visible_toast = true;
                return false;
            }
            //console.log(_this.product);
            //ajax--todo
            _this.fix_base();
        },
        //
        fix_base: function() {
            let _this = this;
            let post_data = {
                product:_this.product,
            };
            this.$http.post(this.products_url, post_data, {
                    before: function() {
                        _this.loading = true;
                    }
                })
                .then(function(response) {
                    _this.loading = false;
                    let data_return = response.body;
                    if (data_return.respcd == '0000') {
                        _this.visible_toast = true;
                        _this.toastmsg = '!';
                    } else {
                        if (data_return.respmsg) {
                            _this.toastmsg = data_return.respmsg;
                        } else {
                            _this.toastmsg = data_return.resperr;
                        }
                        _this.visible_toast = true;
                    }
                }, function(response) {
                    _this.loading = false;
                    _this.visible_toast = true;
                    _this.toastmsg = '!';
                })
                .catch(function(response) {
                    _this.loading = false;
                });
        },
        goback: function() {
            this.$router.go(-1);
        },
        //
        handleSelectionChange(val) {
            let _this = this;
            let select_val = val;
            _this.product = [];
            _this.multipleSelection = val;
            for (let i = 0; i < select_val.length; i++) {
                let id = select_val[i].product_id;
                _this.product.push(id);
            }
            //console.log(_this.product);
        },
        //checkbox
        if_check(row, index) {
            let row_val = row;
            let _this = this;
            if (row_val.__ob__.value.status == '0') {
                return false;
            } else {
                return true;
            }
        },
        //
        get_products: function() {
            var _this = this;
            this.$http.get(this.products_url, {
                    before: function() {
                        _this.loading = true;
                    }
                })
                .then(function(response) {
                    _this.loading = false;
                    var data_return = response.body;
                    if (data_return.respcd == '0000') {
                        _this.products = data_return.data;
                        setTimeout(() => {
                            for (let i = 0; i < _this.products.length; i++) {
                                if (_this.products[i].status == '0') {
                                    _this.product.push(_this.products[i].product_id);
                                    _this.product_start++;
                                    _this.$refs.products.toggleRowSelection(_this.products[i], true);
                                }
                            }
                        }, 0);
                    } else {
                        if (data_return.respmsg) {
                            _this.toastmsg = data_return.respmsg;
                        } else {
                            _this.toastmsg = data_return.resperr;
                        }
                        _this.visible_toast = true;
                    }
                }, function(response) {
                    _this.loading = false;
                    _this.visible_toast = true;
                    _this.toastmsg = '!';
                })
                .catch(function(response) {
                    _this.loading = false;
                });
            // setTimeout(() => {
            //     _this.getval_test();
            // }, 0);
        },
        //
        getval_test: function() {
            var _this = this;
            _this.products = [{
                "userid": 123, // 
                "product_id": 12344, // 
                "name": "", // 
                "intro": "", // 
                "status": 0, // : 0 , 1 
            }, {
                "userid": 123, // 
                "product_id": 33355, // 
                "name": "", // 
                "intro": "", // 
                "status": 0, // : 0 , 1 
            }, {
                "userid": 123, // 
                "product_id": 44466, // 
                "name": "", // 
                "intro": "", // 
                "status": 1, // : 0 , 1 
            }, {
                "userid": 123, // 
                "product_id": 555, // 
                "name": "", // 
                "intro": "", // 
                "status": 1, // : 0 , 1 
            }, {
                "userid": 123, // 
                "product_id": 666, // 
                "name": "", // 
                "intro": "", // 
                "status": 0, // : 0 , 1 
            }];
            //console.log(_this.$refs.products);
            // for (let i = 0; i < _this.products.length; i++) {
            //     if (_this.products[i].status == '0') {
            //         _this.product.push(_this.products[i].product_id);
            //         _this.$refs.products.toggleRowSelection(_this.products[i], true);
            //     }
            // }
            setTimeout(() => {
                for (let i = 0; i < _this.products.length; i++) {
                    if (_this.products[i].status == '0') {
                        _this.product.push(_this.products[i].product_id);
                        _this.product_start++;
                        _this.$refs.products.toggleRowSelection(_this.products[i], true);
                    }
                }
            }, 0);
        },
    }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" rel="stylesheet/less">
</style>



