<template>
    <div>
        <div class="right_head">
            
        </div>
        <div class="right_body">
            <el-form :model="account" :rules="rules_account" ref="account" label-width="125px" class="demo-ruleForm">
                <el-row>
                    <div class="step_show">
                        <el-col :xs="24" :sm="24" :md="12" :lg="8">
                            <el-form-item label="" prop="bankuser">
                                <el-input v-model="account.bankuser" :disabled="true"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :xs="24" :sm="24" :md="12" :lg="8">
                            <el-form-item label="" prop="bankaccount">
                                <el-input v-model="account.bankaccount" :disabled="true"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :xs="24" :sm="24" :md="12" :lg="8">
                            <el-form-item label="" prop="headbankname">
                                <el-select v-model="account.headbankname" placeholder="" disabled>
                                    <el-option v-for="headbankname in headbanks" v-bind:bankid="headbankname.headbankid" v-bind:value="headbankname.headbankname" v-bind:label="headbankname.headbankname">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :xs="24" :sm="24" :md="12" :lg="8">
                            <el-form-item label="" prop="bankname">
                                <el-input v-model="account.bankname" :disabled="true"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :xs="24" :sm="24" :md="24" :lg="24">
                            <div class="clearfix_liner1"></div>
                        </el-col>
                        <el-col :xs="24" :sm="24" :md="12" :lg="8">
                            <el-form-item label="" prop="banktype">
                                <el-select v-model.number="account.banktype" placeholder="" disabled>
                                    <el-option v-for="banktype in banktypes.records" v-bind:value="banktype.tp" v-bind:label="banktype.text">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :xs="24" :sm="24" :md="12" :lg="8">
                            <el-form-item label="" prop="settle_cycle">
                                <el-select v-model.number="account.settle_cycle" placeholder="" disabled>
                                    <el-option v-for="cycle in settle_cycle.records" v-bind:value="cycle.tp" v-bind:label="cycle.text">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :xs="24" :sm="24" :md="12" :lg="8">
                            <el-form-item label="" prop="settle_base_amt">
                                <el-input v-model.number="account.settle_base_amt" :disabled="true">
                                    <template slot="append"></template>
                                </el-input>
                            </el-form-item>
                        </el-col>
                        <!-- <el-col :xs="24" :sm="24" :md="12" :lg="8">
                            <el-form-item label="" prop="fee">
                                <el-input v-model.number="account.fee" :disabled="true">
                                    <template slot="append">%</template>
                                </el-input>
                            </el-form-item>
                        </el-col> -->
                        <el-col :xs="24" :sm="24" :md="12" :lg="8">
                            <el-form-item label="" prop="wechat_fee">
                                <el-input v-model.number="account.wechat_fee" :disabled="true">
                                    <template slot="append">%</template>
                                </el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :xs="24" :sm="24" :md="12" :lg="8">
                            <el-form-item label="" prop="alipay_fee">
                                <el-input v-model.number="account.alipay_fee" :disabled="true">
                                    <template slot="append">%</template>
                                </el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :xs="24" :sm="24" :md="12" :lg="8">
                            <el-form-item label="QQ" prop="qqwallet_fee">
                                <el-input v-model.number="account.qqwallet_fee" :disabled="true">
                                    <template slot="append">%</template>
                                </el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :xs="24" :sm="24" :md="12" :lg="8">
                            <el-form-item label="" prop="jd_fee">
                                <el-input v-model.number="account.jd_fee" :disabled="true">
                                    <template slot="append">%</template>
                                </el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :xs="24" :sm="24" :md="12" :lg="8">
                            <el-form-item label="" prop="swipecard_fee">
                                <el-input v-model.number="account.swipecard_fee" :disabled="true">
                                    <template slot="append">%</template>
                                </el-input>
                            </el-form-item>
                        </el-col>
                    </div>
                    <div class="m_t_20 t_c">
                        <el-col :xs="24" :sm="24" :md="24" :lg="24">
                            <el-button @click="goback"></el-button>
                        </el-col>
                    </div>
                </el-row>
            </el-form>
        </div>
        <load :visible="loading"></load>
        <toast :msg="toastmsg" :visible="visible_toast" @on-visible-change="onVisibleChange" @on-msg-change="onMsgChange"></toast>
    </div>
</template>
<script>
import load from '../../components/load'
import toast from '../../components/toast'
export default {
    name: 'channel_account',
    components: {
        load,
        toast
    },
    data() {
        var checkamt = (rule, value, callback) => {
            if (!value) {
                return callback(new Error(''));
            }
            let reg = /^[0-9]{0,9}$/;
            value = value.toString().replace(/\D/g, '');
            setTimeout(() => {
                if (!reg.test(value)) {
                    callback(new Error(',9'));
                } else {
                    callback();
                }
            }, 1000);
        };
        return {
            bank_error: false,
            bankname_error: false,
            loading: false, //load
            toastmsg: '', //
            visible_toast: false, //toast
            banktypes: {}, //ajax
            settle_cycle: {}, //ajax
            headbanks: [], //ajax
            branchbanks: [], //ajax
            account: {
                "userid": '', // 
                "bankaccount": "", // ()
                "bankuser": "", // ()
                "headbankname": "", // 
                "bankname": "", // (/)
                "banktype": '', // (): 1 , 2 
                "settle_cycle": '', // (): 1 , 2 , 3 
                "settle_base_amt": '', // , : 
                "fee": '', // , : 
                "wechat_fee": '', // 
                "alipay_fee": '', // 
                "jd_fee": '', // 
                "qqwallet_fee": '', // QQ 
                "swipecard_fee": '', // 
            },
            headbankid: '', //-id
            qd_account_url: location.protocol + '//' + location.host + '/qudao/v1/api/qd/' + this.$route.params.userid + '/account_info', //
            qd_headbanks_url: location.protocol + '//' + location.host + '/qudao/v1/api/tools/headbanks', //
            qd_branchbanks_url: location.protocol + '//' + location.host + '/qudao/v1/api/tools/branchbanks', //
            qd_banktypes_url: location.protocol + '//' + location.host + '/qudao/v1/api/tools/banktypes', //
            qd_cycle_url: location.protocol + '//' + location.host + '/qudao/v1/api/tools/settle_cycle', //
            banktypes_over: false, //true
            settle_cycle_over: false, //true
            headbanks_over: false, //true
            rules_account: {
                bankuser: [{
                    required: true,
                    pattern: /^[\u4e00-\u9fa5]{1,4}$/,
                    message: ' 1  4 ',
                    trigger: 'blur'
                }],
                bankaccount: [{
                    required: true,
                    pattern: /^[0-9]{10,19}$/,
                    message: ' 10  19 ',
                    trigger: 'blur'
                }],
                settle_base_amt: [{
                    validator: checkamt,
                    // type: 'number',
                    // required: false,
                    // len:8,
                    // message: ' 1  8 ',
                    trigger: 'blur'
                }],
                fee: [{
                    type: 'number',
                    required: true,
                    max: 100,
                    message: '',
                    trigger: 'blur'
                }],
                banktype: [{
                    type: "number",
                    required: false,
                    message: '',
                    trigger: 'change'
                }],
                settle_cycle: [{
                    type: "number",
                    required: false,
                    message: '',
                    trigger: 'change'
                }],
                headbankname: [{
                    required: true,
                    message: '',
                    trigger: 'change'
                }],
                bankname: [{
                    required: true,
                    message: '',
                    trigger: 'blur'
                }],
                wechat_fee: [{
                    type: 'number',
                    required: true,
                    max: 100,
                    message: '',
                    trigger: 'blur'
                }],
                alipay_fee: [{
                    type: 'number',
                    required: true,
                    max: 100,
                    message: '',
                    trigger: 'blur'
                }],
                jd_fee: [{
                    type: 'number',
                    required: true,
                    max: 100,
                    message: '',
                    trigger: 'blur'
                }],
                qqwallet_fee: [{
                    type: 'number',
                    required: true,
                    max: 100,
                    message: '',
                    trigger: 'blur'
                }],
                swipecard_fee: [{
                    type: 'number',
                    required: true,
                    max: 100,
                    message: '',
                    trigger: 'blur'
                }],
            },
        }
    },
    created: function() {
        this.get_banktypes(); //       
        this.get_cycle(); //      
        this.get_banks(); // 
    },
    watch: {
        banktypes_over: function(val, oldVal) {
            let _this = this;
            if (_this.banktypes_over && _this.settle_cycle_over && _this.headbanks_over) {
                _this.get_account(); //
            }
        },
        settle_cycle_over: function(val, oldVal) {
            let _this = this;
            if (_this.banktypes_over && _this.settle_cycle_over && _this.headbanks_over) {
                _this.get_account(); //
            }
        },
        headbanks_over: function(val, oldVal) {
            let _this = this;
            if (_this.banktypes_over && _this.settle_cycle_over && _this.headbanks_over) {
                _this.get_account(); //
            }
        },
    },
    methods: {
        //toast
        onVisibleChange(val) {
            this.visible_toast = val; //
        },
        //toast
        onMsgChange(val) {
            this.toastmsg = val; //
        },
        goback: function() {
            this.$router.go(-1);
        },
        //
        select_bank: function() {
            // var index = e.target.selectedIndex;
            // var select_op = e.target[index].attributes.areaid.value;
            // var _this = this;
            // let post_data = {
            //     headbankid: select_op
            // };
            // _this.get_branchbank(post_data);

            var _this = this;
            let select_op = '';
            for (let i = 0; i < _this.headbanks.length; i++) {
                if (_this.headbanks[i].headbankname == _this.account.headbankname) {
                    select_op = _this.headbanks[i].headbankid;
                    break;
                }
            }
            let post_data = {
                'headbankid': select_op,
                'cityid': _this.city_id,
            };
            _this.get_branchbank(post_data);
        },
        //
        get_account: function() {
            var _this = this;
            this.$http.get(this.qd_account_url, {
                    before: function() {
                        _this.loading = true;
                    }
                })
                .then(function(response) {
                    _this.loading = false;
                    var data_return = response.body;
                    if (data_return.respcd == '0000') {
                        // //
                        _this.account = data_return.data;
                        _this.account.settle_base_amt = (data_return.data.settle_base_amt / 100).toFixed(0);
                    } else {
                        if (data_return.respmsg) {
                            _this.toastmsg = data_return.respmsg;
                        } else {
                            _this.toastmsg = data_return.resperr;
                        }
                        _this.visible_toast = true;
                    }
                }, function(response) {
                    _this.loading = false;
                    _this.visible_toast = true;
                    _this.toastmsg = '!';
                })
                .catch(function(response) {
                    _this.loading = false;
                });
            // let return_data = {
            //     "userid": 123, // 
            //     "bankaccount": "623300000", // ()
            //     "bankuser": "", // ()
            //     "headbankname": "", // 
            //     "bankname": "", // (/)
            //     "banktype": 1, // (): 1 , 2 
            //     "settle_cycle": 1, // (): 1 , 2 , 3 
            //     "settle_base_amt": 3000000, // , : 
            //     "fee": 1.5, // , : 
            // };
            // //
            // _this.account = return_data;
            // _this.account.settle_base_amt = (return_data.settle_base_amt / 100).toFixed(0);
        },
        //
        get_banktypes: function() {
            var _this = this;
            this.$http.get(this.qd_banktypes_url, {
                    before: function() {
                        _this.loading = true;
                    }
                })
                .then(function(response) {
                    _this.loading = false;
                    var data_return = response.body;
                    if (data_return.respcd == '0000') {
                        _this.banktypes = data_return.data;
                        //
                        _this.account.banktype = _this.banktypes.default;
                        _this.banktypes_over = true;
                    } else {
                        if (data_return.respmsg) {
                            _this.toastmsg = data_return.respmsg;
                        } else {
                            _this.toastmsg = data_return.resperr;
                        }
                        _this.visible_toast = true;
                    }
                }, function(response) {
                    _this.loading = false;
                    _this.visible_toast = true;
                    _this.toastmsg = '!';
                })
                .catch(function(response) {
                    _this.loading = false;
                });
            // _this.banktypes = {
            //     "records": [{
            //         "tp": 1,
            //         "text": ""
            //     }, {
            //         "tp": 2,
            //         "text": ""
            //     }],
            //     "default": 2 // tp
            // };
            // //
            // _this.account.banktype = _this.banktypes.default;
            // _this.banktypes_over=true;
        },
        //
        get_cycle: function() {
            var _this = this;
            this.$http.get(this.qd_cycle_url, {
                    before: function() {
                        _this.loading = true;
                    }
                })
                .then(function(response) {
                    _this.loading = false;
                    var data_return = response.body;
                    if (data_return.respcd == '0000') {
                        _this.settle_cycle = data_return.data;
                        //
                        _this.account.settle_cycle = _this.settle_cycle.default;
                        _this.settle_cycle_over = true;
                    } else {
                        if (data_return.respmsg) {
                            _this.toastmsg = data_return.respmsg;
                        } else {
                            _this.toastmsg = data_return.resperr;
                        }
                        _this.visible_toast = true;
                    }
                }, function(response) {
                    _this.loading = false;
                    _this.visible_toast = true;
                    _this.toastmsg = '!';
                })
                .catch(function(response) {
                    _this.loading = false;
                });
            // _this.settle_cycle = {
            //     "records": [{
            //         "tp": 1,
            //         "text": ""
            //     }, {
            //         "tp": 2,
            //         "text": ""
            //     }, {
            //         "tp": 3,
            //         "text": ""
            //     }, ],
            //     "default": 2 // tp
            // };
            // //
            // _this.account.settle_cycle = _this.settle_cycle.default;
            // _this.settle_cycle_over=true;
        },
        //
        get_banks: function() {
            var _this = this;
            this.$http.get(this.qd_headbanks_url, {
                    before: function() {
                        _this.loading = true;
                    }
                })
                .then(function(response) {
                    _this.loading = false;
                    var data_return = response.body;
                    if (data_return.respcd == '0000') {
                        _this.headbanks = data_return.data.records;
                        // 
                        _this.account.headbankname = _this.headbanks[0].headbankname;
                        //
                        // let post_data={headbankid:_this.headbanks[0].headbankid};
                        // _this.get_branchbank(post_data);                        
                        _this.headbanks_over = true;
                    } else {
                        if (data_return.respmsg) {
                            _this.toastmsg = data_return.respmsg;
                        } else {
                            _this.toastmsg = data_return.resperr;
                        }
                        _this.visible_toast = true;
                    }
                }, function(response) {
                    _this.loading = false;
                    _this.visible_toast = true;
                    _this.toastmsg = '!';
                })
                .catch(function(response) {
                    _this.loading = false;
                });
            // _this.headbanks = [{
            //     "headbankid": 1111, //id
            //     "headbankname": '', //
            //     "iscommon": 1, //10
            //     "csphone": '95555' //
            // }, {
            //     "headbankid": 222, //id
            //     "headbankname": '', //
            //     "iscommon": 1, //10
            //     "csphone": '95555' //
            // }, {
            //     "headbankid": 3333, //id
            //     "headbankname": '', //
            //     "iscommon": 1, //10
            //     "csphone": '95555' //
            // }];
            // // 
            // _this.account.headbankname = _this.headbanks[0].headbankname;
            // _this.get_branchbank();
        },
        //
        get_branchbank: function(post_data) {
            var _this = this;
            this.$http.get(this.qd_branchbanks_url, post_data, {
                    before: function() {
                        _this.loading = true;
                    }
                })
                .then(function(response) {
                    _this.loading = false;
                    var data_return = response.body;
                    if (data_return.respcd == '0000') {
                        _this.branchbanks = data_return.data.records;
                        //
                        _this.account.bankname = _this.branchbanks[0].name;
                        _this.headbanks_over = true;
                    } else {
                        if (data_return.respmsg) {
                            _this.toastmsg = data_return.respmsg;
                        } else {
                            _this.toastmsg = data_return.resperr;
                        }
                        _this.visible_toast = true;
                    }
                }, function(response) {
                    _this.loading = false;
                    _this.visible_toast = true;
                    _this.toastmsg = '!';
                })
                .catch(function(response) {
                    _this.loading = false;
                });
            // _this.branchbanks = [{
            //     "name": "" //
            // }];
            // //
            // _this.account.bankname = _this.branchbanks[0].name;
            // _this.headbanks_over=true;
        },
    }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" rel="stylesheet/less">
</style>



